.header {
    background: rgba(179,186,192, 0.2);
    height: fit-content;
    padding-top: 40px;
    padding-bottom: 16px;
    @media screen and ( max-width: 720px ) {
        padding-top: 0; } }

.wrapper {
    display: flex;
    justify-content: space-between;
    width: 100%;
    @media screen and ( max-width: 720px ) {
        flex-direction: column;
        justify-content: center;
        align-items: center; } }
