.wrapper {
    display: flex;
    padding-left: 19px;
    align-items: center;

    .skill {
        width: fit-content;
        font-weight: bold;
        font-size: 16px;
        line-height: 20px;
        position: relative;
        margin: 4px 0;
        &::before {
            content: '–';
            left: -19px;
            position: absolute; } } }
.input {
    margin-left: 8px;
    width: 70px !important;
    height: 20px;
    font-size: 16px;
    line-height: 20px;
    color: #3483CC;
    border-bottom: 1px dotted #3483CC; }
